*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: 'Open Sans', sans-serif;
}

body {
  font-family: inherit;
}

@font-face {
  font-family: 'Pixel';
  src: url('fonts/Pixel-LCD-7.woff') format('woff');
}

/* VARIABLES */

:root {
  --color-white: #fff;
  --color-light-white: #eee;

  --color-tomato: #d40640;
  --color-purple-transparent: rgba(88, 24, 68, .7);
  --color-pink-transparent: rgba(216, 14, 91, .7);

  --color-orange: #ff3c00;

  --color-light-black: #222;
  --color-black: #000;

  --shadow: rgba(0, 0, 0, .4);
}